import { OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { DataService } from '../data.service';
var LoginComponent = /** @class */ (function () {
    function LoginComponent(formBuilder, ds, route, router) {
        this.formBuilder = formBuilder;
        this.ds = ds;
        this.route = route;
        this.router = router;
        this.submitted = false;
    }
    LoginComponent.prototype.ngOnInit = function () {
        this.registerForm = this.formBuilder.group({
            userName: ['', Validators.required],
            password: ['', Validators.required]
        });
        // this.ds.getAdmin("", "").subscribe( (info: any) => {
        //   this.data = info.data;
        // })
        this.data = null;
    };
    Object.defineProperty(LoginComponent.prototype, "f", {
        get: function () { return this.registerForm.controls; },
        enumerable: true,
        configurable: true
    });
    LoginComponent.prototype.onSubmit = function () {
        var _this = this;
        this.submitted = true;
        // stop here if form is invalid
        if (this.registerForm.invalid) {
            return;
        }
        var name = this.registerForm.value.userName;
        var pass = this.registerForm.value.password;
        this.ds.getAdmin(name, pass).subscribe(function (info) {
            _this.data = info.data;
            // console.log("DATA:"+JSON.stringify(this.data))
            if (_this.data !== undefined && _this.data !== null) {
                localStorage.setItem('token', _this.data.token);
                _this.router.navigate(['/dashboard']);
            }
            else {
                alert("Failed :-(");
            }
        });
    };
    return LoginComponent;
}());
export { LoginComponent };
