import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { Router } from '@angular/router';
import { AuthServiceService } from '../auth-service.service'

@Component({
  selector: 'app-nav',
  templateUrl: './nav.component.html',
  styleUrls: ['./nav.component.css']
})
export class NavComponent implements OnInit {

  dataModal = false;

  constructor(
    private ds: DataService,
    private router: Router,
    public authService: AuthServiceService
  ) { }

  ngOnInit() {
    
  }

  dashboardBtn(){
    // alert(window.location.href)
    
  }

  searchSubmit(){
    let key = $('#selectOption option:selected').val();
    let value = $('#searchKey').val();
    // alert(key+"   "+value)

    // this.data = await this.ds.getSearchResult(key, value)
    // console.log("Data: "+JSON.stringify(this.data))
    if(key === "" || value === "")
    {
      this.dataModal = false;
      $("#searchKey").css({
        "border":"solid 2px red"
      });
    }
    else
    {
      this.dataModal = false;
      $("#searchKey").css({
        "border":"1px solid #ced4da"
      });
      this.ds.getSearchResult(key, value)
      .then(success => {
        // console.log("NAV-COMPONENT:"+JSON.stringify(success))
        // console.log((success.data.length)?true:false);
        if(success["data"].length)
        {
          if(key === "aliasId" || key === "alias")
          {
            // alert("alias")
            // window.location.reload();
            this.dataModal = true;
            this.router.navigate(['/aliasList']);
          }
          else
          {
            // alert("rest")
            this.dataModal = true;
            this.router.navigate(['/userList']);
          }
        }
        else{
          // alert("else")
          this.dataModal = false;
          $("#searchKey").css({
            "border":"solid 2px red"
          });
        }
      })
      .catch(err => {
        console.log(err)
      })
    }
  }

}
