import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-alias-details',
  templateUrl: './alias-details.component.html',
  styleUrls: ['./alias-details.component.css']
})
export class AliasDetailsComponent implements OnInit {

  data:any;
  alias:any;
  inbox:any;
  sent:any;

  constructor(
    private ds: DataService,
    private router: Router
  ) {
    this.alias = this.router.getCurrentNavigation().extras.state.alias;
  }

  ngOnInit() {
    this.ds.aliasMailData(this.alias)
      .then(info => {
        let data = info["data"][0];
        // alert(JSON.stringify(data))
        this.ds.getInboxDetails(data.wildduckId, data.inboxId)
          .then(res=>{
            this.inbox = res;
            // alert(JSON.stringify(this.inbox))
          })
          .catch(err => {
            console.log(err);
          })
        this.ds.getOutboxDetails(data.wildduckId, data.sentId)
          .then(res=>{
            this.sent = res;
          })
          .catch(err => {
            console.log(err);
          })
      })
      .catch(err => {
        console.log(err);
      })
    this.data = localStorage.getItem("aliasDetails");
    // alert(this.data);
    this.data = JSON.parse(this.data);
    this.data = this.data["data"][0];
    console.log(this.data);
  }

}
