    DataService.prototype.getUser = function (id) {
        var authService = this.injector.get(AuthServiceService);
        return new Promise(function (resolve, reject) {
            $.ajax({
                type: "GET",
                // url:`http://localhost:9001/api/v1/admin/user/${id}`,
                url: "https://jinnmailapp.herokuapp.com/api/v1/admin/user/" + id,
                beforeSend: function (xhr) { xhr.setRequestHeader('Authorization', "" + authService.getToken()); },
                success: function (success) {
                    // console.log("SUCCESS: "+JSON.stringify(success));
                    localStorage.setItem("userDetails", JSON.stringify(success));
                    resolve(success);
                },
                error: function (err) {
                    console.log(err);
                    reject({ code: 500, msg: 'err' });
                },
                contentType: 'application/json'
            });
        });
    };
