import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router'
import { DataService } from '../data.service'

@Component({
  selector: 'app-user-details',
  templateUrl: './user-details.component.html',
  styleUrls: ['./user-details.component.css']
})
export class UserDetailsComponent implements OnInit {

  data:any;
  id: any;
  aliases = [];
  inbox:any;
  sent:any;
  constructor(
    private router: Router,
    private ds: DataService
  ) {
    // alert(this.router.getCurrentNavigation().extras.state.id);
    // this.id = this.router.getCurrentNavigation().extras.state.id;
  }

  ngOnInit() {
    this.data = localStorage.getItem("userDetails");
    // alert(this.data);
    this.data = JSON.parse(this.data);
    this.data = this.data["data"][0];
    this.aliases = this.data.aliases;
    this.aliases = this.aliases.reverse();
    // alert("Alias:"+JSON.stringify(this.aliases))
    this.sent = this.inbox = {
      data:{
        mostRecent:"None"
      }
    }
    // this.getAliasData(this.aliases[0].alias)
    // console.log(this.aliases);
    // console.log(this.data);
    // this.ds.getUser(this.id)
    //   .then((info) => {
    //     // alert(JSON.stringify(info))
    //     this.data = info["data"][0];
    //     this.aliases = this.data.aliases;
    //     this.aliases = this.aliases.reverse();
    //     console.log(this.aliases);
    //     console.log(this.data);
    //   })
    //   .catch(err => {
    //     console.log(err);
    //   })

  }
  getAliasData(alias, i){
    // alert(alias+" "+i)
    this.ds.aliasMailData(alias)
      .then(info => {
        let data = info["data"][0];
        // alert(JSON.stringify(data))
        this.ds.getInboxDetails(data.wildduckId, data.inboxId)
          .then(res=>{
            this.inbox = res;
            // alert(JSON.stringify(this.inbox))
          })
          .catch(err => {
            console.log(err);
          })
        this.ds.getOutboxDetails(data.wildduckId, data.sentId)
          .then(res=>{
            this.sent = res;
          })
          .catch(err => {
            console.log(err);
          })
      })
      .catch(err => {
        console.log(err);
      })
  }

}
