  getAdmin(uname, pwd){
    // console.log(uname+"---"+pwd);
    this.data = {
      'username' : uname,
      'password' : pwd
    }
    this.config = {
      headers : new HttpHeaders().set('Content-Type', 'application/json')
    }
    // return this.http.post<any>("http://localhost:9001/api/v1/admin", this.data, this.config);
    return this.http.post<any>("https://jinnmailapp.herokuapp.com/api/v1/admin", this.data, this.config);
  }

  getUser(id){
    let authService = this.injector.get(AuthServiceService)
    return new Promise((resolve, reject)=> {
      $.ajax({
        type:"GET",
        // url:`http://localhost:9001/api/v1/admin/user/${id}`,
        url:`https://jinnmailapp.herokuapp.com/api/v1/admin/user/${id}`,
        beforeSend: function(xhr){xhr.setRequestHeader('Authorization', `${authService.getToken()}`);},
        success: (success) => {
          // console.log("SUCCESS: "+JSON.stringify(success));
          localStorage.setItem("userDetails", JSON.stringify(success));
          resolve(success)
        },
        error: (err) => {
            console.log(err)
            reject({ code:500, msg:'err'})
        },
        contentType: 'application/json'
      })       
    })
  }

