  getSearchResult(key, value){
    let authService = this.injector.get(AuthServiceService)
    localStorage.removeItem("searchResult");
    return new Promise((resolve, reject)=> {
      $.ajax({
        type:"GET",
        // url:`http://localhost:9001/api/v1/admin/search/${key}/${value}`,
        url:`https://jinnmailapp.herokuapp.com/api/v1/admin/search/${key}/${value}`,
        beforeSend: function(xhr){xhr.setRequestHeader('Authorization', `${authService.getToken()}`);},
        success: (success) => {
          console.log("SUCCESS: "+JSON.stringify(success));
          localStorage.setItem("searchResult", JSON.stringify(success));
          // alert(success.data[0].verified)
          if(success.data[0].verified)
          {
            localStorage.removeItem("userDetails");
            alert(JSON.stringify(success))
            localStorage.setItem("userDetails", JSON.stringify(success));
          }
          else
          {
            localStorage.removeItem("aliasDetails");
            localStorage.setItem("aliasDetails", JSON.stringify(success));
          }
          this.searched = success; 
          resolve(this.searched)
        },
        error: (err) => {
            // alert(err.responseJSON.error)
            console.log(err)
            reject({ code:500, msg:'err'})
        },
        contentType: 'application/json'
      })       
    })
  }
