import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css']
})
export class SearchComponent implements OnInit {

  constructor() { }

  ngOnInit() {
  }

  search(event: any){
    // console.log(event)
    $("tr").show()
    let tr:any = $("tr");
    tr.splice(0,1);
    // console.log(tr);
    let x = tr.each((d,i)=>{})
    // console.log(x);
    let col = x.filter((i) => {
      // console.log(x[i].innerText);
      let v = x[i].innerText;
      return !v.includes(event.target.value);
    })
    if(col)
    {
      col.hide()
    }
  }
  

}
